import {
    Card,
    Breadcrumb,
    Form,
    Button,
    Radio,
    Input,
    Upload,
    Space,
    Select,
    message
} from 'antd'
import { PlusOutlined } from '@ant-design/icons'
import { Link, useNavigate, useSearchParams } from 'react-router-dom'
import './index.scss'

import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { useStore } from '@/store';
import { useEffect, useRef, useState } from 'react';
import { observer } from 'mobx-react-lite';
import { http } from '@/utils';

const { Option } = Select

const OperationArticle = (props) => {

    const { channelStore: { channelList } } = useStore()
    // 
    const [fileList, setFileList] = useState([])
    // useRef
    const cacheImgList = useRef([])
    const onUploadChange = ({ fileList }) => {
        // logresponse
        // react state fileListresponse.data.url
        // 
        const formatList = fileList.map(file => {
            if (file.response) {
                return {
                    url: file.response.data.url
                }
            }
            return file
        })
        setFileList(formatList)
        // 
        cacheImgList.current = formatList
    }

    // 
    const [imgCount, setImageCount] = useState(1)
    const radioChange = (e) => {
        const { current } = cacheImgList
        const { target: { value } } = e
        setImageCount(value)
        // 
        if (current.length === 0) { return }
        if (value === 1) {
            setFileList([current[0]])
        } else if (value === 3) {
            setFileList(current)
        }
    }
    const navigate = useNavigate()

    // const [params] = useSearchParams()
    // const articleId = params.get('id')
    const { articleId } = props
    // 
    const onFinish = async (values) => {
        // cover
        const { type } = values
        const cover = {
            type,
            images: fileList.map(item => item.url)
        }
        const data = { ...values, cover }
        if (articleId) {
            await http.put(`/mp/articles/${articleId}?draft=false`, data)
        } else {
            await http.post('/mp/articles?draft=false', data)
        }

        //  
        navigate('/article')
        message.success(`${articleId ? '' : ''}!`)
    }

    // id
    // console.log('route', articleId);
    //  id 1. 2. 3.UploadfileList
    const formRef = useRef(null)
    useEffect(() => {
        const loadDetail = async () => {
            const res = await http.get(`/mp/articles/${articleId}`)
            const { data, data: { cover } } = res
            formRef.current.setFieldsValue({ ...data, type: cover.type })
            // setFileList
            const imgUrls = cover.images.map(url => ({ url }))
            setFileList(imgUrls)
            // 
            cacheImgList.current = imgUrls
        }
        // 
        if (articleId) {
            loadDetail()
        }
    }, [articleId])

    return (
        <div className="publish">
            <Card
                title={
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item>
                            <Link to="/"></Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>{articleId ? '' : ''}</Breadcrumb.Item>
                    </Breadcrumb>
                }
            >
                <Form
                    labelCol={{ span: 4 }}
                    wrapperCol={{ span: 16 }}
                    initialValues={{ type: 1, content: '' }}
                    onFinish={onFinish}
                    ref={formRef}
                >
                    <Form.Item
                        label=""
                        name="title"
                        rules={[{ required: true, message: '' }]}
                    >
                        <Input placeholder="" style={{ width: 400 }} />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="channel_id"
                        rules={[{ required: true, message: '' }]}
                    >
                        <Select placeholder="" style={{ width: 400 }}>
                            {/* <Option value={0}></Option> */}
                            {channelList.map(item => <Option key={item.id} value={item.id}>{item.name}</Option>)}
                        </Select>
                    </Form.Item>

                    <Form.Item label="">
                        <Form.Item name="type">
                            <Radio.Group onChange={radioChange}>
                                <Radio value={1}></Radio>
                                <Radio value={3}></Radio>
                                <Radio value={0}></Radio>
                            </Radio.Group>
                        </Form.Item>
                        {imgCount > 0 && (<Upload
                            name="image"
                            listType="picture-card"
                            className="avatar-uploader"
                            showUploadList
                            action="http://geek.itheima.net/v1_0/upload"
                            fileList={fileList}
                            onChange={onUploadChange}
                            maxCount={imgCount}
                            multiple={imgCount > 1}
                        >
                            {fileList.length !== imgCount && (<div style={{ marginTop: 8 }}>
                                <PlusOutlined />
                            </div>)}
                        </Upload>)}
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="content"
                        rules={[{ required: true, message: '' }]}
                    >
                        <ReactQuill theme="snow" />
                    </Form.Item>

                    <Form.Item wrapperCol={{ offset: 4 }}>
                        <Space>
                            <Button size="large" type="primary" htmlType="submit">
                                {articleId ? '' : ''}
                            </Button>
                        </Space>
                    </Form.Item>
                </Form>
            </Card>
        </div>
    )
}

export default observer(OperationArticle) 